import Common from 'common/common.js'
import axios from 'axios'

export default {
	data() {
		return{
            // 
            showMask: false,
            // 
            percentage: 0,
        }
    },
    created(){},
	methods: {
		// 
        viewImage(){
            this.showModal = true;
        },
        // 
        uploadClick(){
            // 
            this.$refs.imgFile.click();
        },
        // 
        selectFile(event){
            // 
            this.progressShow();

            // 
            const fileList = this.$refs.imgFile.files;
            if(fileList.length > 0){
                let fileName = fileList[0].name;
                // kb
                let fileSize = Math.floor(fileList[0].size / 1024);

                let maxSize = this.fileSize;                
                let overHint = maxSize + 'kb';
                // 
                if(fileSize > maxSize){
                    if(maxSize > 1024) overHint = Math.floor(maxSize/1024) + 'M';
                    this.$Notice.warning({
                        title: '',
                        desc: ' ' + fileName + '  ' + overHint
                    });
                    this.progressHide();
                    return false;
                }
                this.uploadFile(fileList[0]);
            }
            else console.log('');
        },
        // 
        uploadFile(file){
            // 
            let progress = setInterval(() => {
                if(this.percentage == 90) clearInterval(progress);
                this.percentage += 10;
            },100);

            // formData
            let params = new FormData();
            // tokentokentoken
            params.append('token', Common.UPLOAD_TOKEN);
            params.append('file', file);
            
            // incorrect region, please use up-z2.qiniu.com
            axios.post('http://upload.qiniu.com/', params)
            .then(res => {
                let url = Common.UPLOAD_URL + res.data.hash;
                this.$store.commit('SET_IMAGE_URL', url);
                // 
                this.progressHide();
                this.percentage = 100;
                clearInterval(progress);
                this.$Notice.success({ title: '!' });
            })
            .catch(err => {
                // 
                this.progressHide();                 
                clearInterval(progress);                
                this.$Notice.error({ title: '' });
            })

            /* Bmob */
            // var this_file = new Bmob.File(fileName, file);     
            // this_file.save().then(function(obj) {
            //     alert(obj.url());
            // }, function(error) {
            //     console.log(error);
            // });
            /* Bmob */
        },
        // 
        progressShow(){
            this.loading = true;
            this.uploadStatus = '';
            this.percentage = 0;
            this.showProgress = true;
        },
        // 
        progressHide(){
            this.loading = false;
            this.uploadStatus = 'finished';
            this.showProgress = false;
        },
        // 
        notFoundPic(event){
            Common.SetDefaultPic(event, 2);
        },
	}
}


