<template>
    <div id='multipleImage' class="m-Upload">
        <!--  -->
        <div :ref="'upload-img-'+index" v-for="(item, index) in getImageUrlArr" :key="index" class="upload-show">
            <div class="upload-img-wrap"><img class="upload-image" :src="item" alt="" @error="notFoundPic"/></div>
            <!--  -->
            <div class="upload-mask">
                <Icon v-if="preview" type="eye" style="margin-right:15px;" @click.native="viewImage(item)"></Icon>
                <Icon type="ios-trash" @click.native="removeImage('upload-img-'+index, index)"></Icon>
            </div>
        </div>
        <!--  -->
        <div>
            <Button type="ghost" :loading="loading" icon="ios-cloud-upload-outline" @click="uploadClick"></Button>
            <span class="size-hint" v-if="sizeHint"> {{ sizeHint }}</span>
            <input ref="imgFile" type="file" :accept="format" hidden @change="selectFile"/>
        </div>
        <!--   -->
        <template v-if="uploadStatus != 'finished'">
            <Progress v-if="showProgress" :percent="percentage" hide-info :stroke-width="3" style="width:340px;"></Progress>
        </template>
        <br/>
        <!--  -->
        <span class="hint">
            * jpgjpegpng
            <span v-if="fileSize < 1024">{{ fileSize }}kb</span>
            <span v-else>{{ Math.floor(fileSize/1024) }}M</span>
            {{ maxNum }}
        </span>
        <div class="clearfix"></div>
        <!--  -->
        <Modal title="" class="m-view-img" v-model="showModal" width="600">
            <img :src="showImgUrl" @error="notFoundPic"/>
            <div slot="footer"></div>
        </Modal>
    </div>
</template>

<script>
    // JS
    import Common from 'common/common.js'
    // 
    import UploadImg from 'mixins/upload_img.js'
    // axios
    import axios from 'axios'
    // Vuex
    import { mapGetters } from 'vuex'

    export default {
        name: 'multipleImage',
        mixins: [ UploadImg ],
        // 
        props: {
            // 
            preview:{
                type: Boolean,
                default: false
            },
            // 
            sizeHint:{
                type: String,
                default: ''
            },
            // KB
            fileSize:{
              type: Number,
              default: 150
            },
            // 
            maxNum: {
                type: Number,
                default: 5
            }
        },
        computed: {
            ...mapGetters([
                // 
                'getImageUrlArr',
            ])
        },
        data () {
            return {
                // 
                loading: false,
                // URL
                showImgUrl: '',
                // 
                showModal: false,
                // 
                format: ['image/jpg', 'image/jpeg', 'image/png'],
                // finished
                uploadStatus: '',
                // 
                showProgress: false,
                // url
                urlArr: [],
            }
        },
        methods: {
            // 
            viewImage(url){
                this.showImgUrl = url;
                this.showModal = true;
            },
            // 
            removeImage(img, index){
                // 
                this.urlArr.splice(index, 1);
                // 
                this.$store.commit('SET_IMAGE_URL_ARR', this.urlArr);
            },
            // 
            uploadFile(file){
                // 
                const check = this.getImageUrlArr.length < this.maxNum;
                if(!check){
                    this.$Notice.warning({
                        title: '',
                        desc: '' + this.maxNum + ''
                    });
                    // 
                    this.progressHide(); 
                    return check;
                }

                // 
                let progress = setInterval(() => {
                    if(this.percentage == 90) clearInterval(progress);
                    this.percentage += 10;
                },100);

                // formData
                let params = new FormData();
                // tokentokentoken
                params.append('token', Common.UPLOAD_TOKEN);
                params.append('file', file);
                
                // 
                axios.post('http://upload.qiniu.com/', params)
                .then(res => {
                    let url = Common.UPLOAD_URL + res.data.hash;
                    this.urlArr.push(url);
                    // 
                    this.$store.commit('SET_IMAGE_URL_ARR', this.urlArr);
                    // 
                    this.progressHide();
                    this.percentage = 100;
                    clearInterval(progress);
                    this.$Notice.success({ title: '!' });
                })
                .catch(err => {
                    // 
                    this.progressHide();                 
                    clearInterval(progress);
                    this.$Notice.error({ title: '' });
                })
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../../../assets/less/upload_img";
</style>


