export default {
	data() {
		return {
			// 
            selectList: [],
		}
	},
	methods: {
        // 
        rowClassName(row, index){
            return row.enabledState == 1 ? '' : 'tr-disable';
        },
		// 
		setSelectList(selection, row){
			// 
			this.selectList = [];
			for(let i = 0 ; i < selection.length ; i ++){
				this.selectList.push(selection[i].id);
            }
        },
        // 
        clearSelect(){
            this.selectList = [];
        },
        // 
        addData(){
            this.apiAdd().then(res => {
                this.pageLoading = false;
                if(res.code == 200){
                    this.$Message.success('!');
                    // 
                    this.updateList();
                    // 
                    this.clearSelect();
                }
                else console.log(res);
            }).catch(err => this.$Message.error(''))
        },
        // 
        EditData(){
            this.apiEdit().then(res => {
                this.pageLoading = false;
                if(res.code == 200){
                    this.$Message.success('!');
                    // 
                    this.updateList();
                    // 
                    this.clearSelect();
                }
                else console.log(res);
            }).catch(err => this.$Message.error(''))
        },
		// 
        deleteData(){
            // 
            this.$Modal.confirm({
                // 
                okText: '',
                // 
                render: (h) => {
                    return h('p', {}, '');
                },
                // 
                onOk: () => {
                    // 
                    setTimeout(() => this.pageLoading = true, 500);
                    
                    this.apiDelete().then(res => {
                        this.pageLoading = false;
                        if(res.code == 200){
                            this.$Message.success('!');
                            // 
                            if(this.page.pageNo > 1 && this.listData.length <= 1){
                                this.page.pageNo--;
                                this.page.pageCount--;
                            }
                            // 
                            this.updateList();
                            // 
                            this.clearSelect();
                        }
                        else console.log(res);
                    }).catch(err => this.$Message.error(''))
                }
            });
        },
        // 
        enableOrDisable(type){
            // 
            setTimeout(() => this.pageLoading = true, 500);

            // 
            if(type === 1){
                // 
                this.apiEnable()
                .then(res => {
                    this.pageLoading = false;
                    if(res.code == 200){
                        this.$Message.success('!');
                        // 
                        this.updateList();
                        // 
                        this.clearSelect();
                    }
                    else this.$Message.warning(res.msg);
                }).catch(err => console.log(err))
            }
            // 
            else if(type === -1){                                
                // 
                this.apiDisable().then(res => {
                    this.pageLoading = false;
                    if(res.code == 200){
                        this.$Message.success('!');
                        // 
                        this.updateList();
                        // 
                        this.clearSelect();
                    }
                    else this.$Message.warning(res.msg);
                }).catch(err => console.log(err))
            }
        },
	}
}


