<template>
    <div class="m-product-info">
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>
            <Form ref="infoForm" :model="infoForm" :rules="validate" :label-width="100">
                <Form-item label="" prop="productName">
                    <Input v-model="infoForm.productName" placeholder=""></Input>
                </Form-item>
                <Form-item label="" prop="productDesc">
                    <quill-editor
                        class="instruction-editor"
                        v-model="infoForm.productDesc"
                        ref="myQuillEditor"
                        :options="editorOption"
                        @ready="onEditorReady($event)"
                    >
                    </quill-editor>
                </Form-item>
                <!--  -->
                <div class="m-operation">
                    <Button class="fr" type="primary" @click="edit('infoForm')"></Button>
                    <div class="clearfix"></div>
                </div>
            </Form>
        </div>
    </div>
</template>

<script>
    // 
    import Loading from 'components/Common/Loading'
    // Api
    import Api from 'api/api.js'
    // Vue-quill-editor
    import Quill from 'quill'
    // QullEditor
    import { ImageImport } from 'qullEditor/ImageImport.js'
    import { ImageResize } from 'qullEditor/ImageResize.js'
    Quill.register('modules/imageImport', ImageImport)
    Quill.register('modules/imageResize', ImageResize)
    
    export default {
        components: { Loading },
        // productId
        props: {
            // 
            productId:{
                type: String,
                default: '',
            }, 
        },
        data() {
            return {
                // 
                pageLoading: true,
                // 
                editorOption:{
                    placeholder: '',
                    modules:{
                        imageImport: true,
                        imageResize: {
                            displaySize: true
                        }
                    }
                },
                // 
                infoForm: {
                    productName: '',
                    productDesc: '',
                },
                // 
                validate: {
                    productName: [{ required: true, message: '', trigger: 'blur'}],
                },
            }
        },
        created() {
            // 
            this.getDetail();
        },
        methods: {
            // 
            onEditorReady(editor) {
            },
            // 
            onEditorChange({ editor, html, text }) {
                this.content = html
            },
            // 
            getDetail(){
                Api.GetProdInfo(this.productId)
                .then(res => {                    
                    // 
                    this.pageLoading = false;
                    const result = res.data.attributes;                    
                    if(res.code == 200){
                        // 
                        this.infoForm = result;
                    }
                    else this.$Message.warning(res.msg);
                })
                .catch(err => console.log(err))
            },
            // 
            edit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        // 
                        this.pageLoading = true;

                        // 
                        Api.EditProduct(this.infoForm, this.productId)
                        .then(res => {
                            // 
                            this.pageLoading = false;
                            if(res.code == 200) this.$Message.success('!');
                            else this.$Message.warning(res.msg);
                        })
                        .catch(err => console.log(err))
                    }
                    else this.$Message.error('');
                })    
            },
        }
    }
</script>

<style lang="less">
    @import "../../../../assets/less/color";
    .m-product-info{
        background: #f5f5f5;
        border-radius: 4px;
        padding: 20px;
    }

    .m-operation {
        button, a {
            min-width: 80px;
            margin-left: 10px;
        }
    }
    
    .instruction-editor{
        background: #fff;
        .ql-toolbar{
            border-radius: 4px 4px 0 0;
            border: 1px solid @table_border_color;
        }
        .ql-container{
            min-height: 500px;
            border-radius: 0 0 4px 4px;
            border: 1px solid #eee;
            .ql-editor{
                padding-bottom: 20px;
            }
        }
    }
</style>


