<template>
    <div class="g-content">
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <Form v-else ref="infoForm" :model="infoForm" :rules="validate" :label-width="120">
            <!--  -->
            <h2 class="m-title"></h2>
            <div class="m-content">
                <Row>
                    <Col span="12">
                        <Form-item v-if="pageType == 'edit'" label="">
                            <span>{{ userId }}</span>
                        </Form-item>
                        <Form-item label="" prop="realname">
                            <Input v-model="infoForm.realname" placeholder=""></Input>
                        </Form-item>
                        <Form-item label="" prop="gender">
                            <Radio-group v-model="infoForm.gender">
                                <Radio label=""></Radio>
                                <Radio label=""></Radio>
                            </Radio-group>
                        </Form-item>
                        <Form-item label="" prop="mobile">
                            <Input v-model="infoForm.mobile" placeholder=""></Input>
                        </Form-item>
                        <Form-item label="" prop="email">
                            <AutoComplete v-model="infoForm.email" :data="emailList" @on-search="selectEmail" @on-select="setEmail" placeholder=""></AutoComplete>
                        </Form-item>
                    </Col>
                    <Col span="12">
                        <Form-item v-if="pageType == 'edit'" label="">
                            <span>{{ createdAt }}</span>
                        </Form-item>
                        <Form-item label="" style="margin-bottom:16px;">
                            <!-- -- -->
                            <SingleImage :preview="true" size-hint="100*100px"></SingleImage>
                        </Form-item>
                        <Form-item label="" prop="birthdate">
                            <Date-picker v-model="infoForm.birthdate" type="date" placeholder="" style="width:100%"></Date-picker>
                            <div class="clearfix"></div>
                        </Form-item>
                    </Col>
                </Row>
            </div>
            <!--  -->
            <h2 class="m-title"></h2>
            <div class="m-content">
                <Row>
                    <Col span="12">
                        <Form-item label="">
                            <!-- - -->
                            <CompanyName type="1"></CompanyName>
                        </Form-item>                        
                        <Form-item label="" prop="profession">
                            <CheckboxGroup v-model="professionValue" @on-change="checkProfess">
                                <Checkbox v-for="(item, index) in professionList" :key="index" :label="item.name"></Checkbox>
                            </CheckboxGroup>
                        </Form-item>
                        <Form-item label="">
                            <Cascader :data="cityList" v-model="provinceValue" placeholder="" @on-change="selectCity"></Cascader>
                        </Form-item>
                    </Col>
                    <Col span="12">
                        <Form-item label="">
                            <Select v-model="infoForm.job" placeholder="">
                                <Option v-for="(item, index) in jobList" :value="item.name" :key="index">{{ item.name }}</Option>
                            </Select>
                        </Form-item>
                        <Form-item label="" prop="workTime">
                            <Date-picker v-model="infoForm.workTime" type="month" :placeholder="workTimePH" :disabled="disabledWT" style="width:85%;margin-right:10px"></Date-picker>
                            <Checkbox v-model="infoForm.isGraduate"></Checkbox>
                        </Form-item>                        
                    </Col>
                </Row>
            </div>
            <!--  -->
            <div class="m-operation">
                <Button v-if="pageType == 'edit'" class="fr" type="primary" @click="submit('infoForm', 'edit')"></Button>
                <Button v-else class="fr" type="primary" @click="submit('infoForm', 'edit')"></Button>
                <Button class="fr" type="ghost" @click="$router.go(-1)"></Button>
                <div class="clearfix"></div>
            </div>
        </Form>
    </div>
</template>

<script>
    // 
    import Loading from 'components/Common/Loading'
    import CompanyName from 'components/Input/FuzzyQuery'
    // 
    import SingleImage from 'components/Image/UploadImage/SingleImage'
    // JS
    import Common from 'common/common.js'
    // 
    import Validate from 'common/validate.js'
    // Api
    import Api from 'api/api.js'
    // 
    import CitySelect from 'mixins/city_select.js'
    // 
    import EmailComplete from 'mixins/email_complete.js'
    // Json
    import JsonCity from 'mock/city.json'
    import JsonData from 'mock/data.json'
    // Vuex
    import { mapGetters } from 'vuex'
    
    export default {
        components: { Loading, CompanyName, SingleImage },
        mixins: [ CitySelect, EmailComplete ],
        computed: {
            ...mapGetters(['companyName', 'getImageUrl' ])
        },
        data() {
            return {
                // 
                pageLoading: true,
                // 
                pageType: 'add',
                // 
                jobList: [],
                // 
                professionList: [],
                // 
                provinceValue:[],
                // 
                professionValue: [],
                // 
                disabledWT: false,
                // 
                workTimePH: '',
                // 
                userId: '',
                // 
                createdAt: '',
                // 
                infoForm: {                    
                    // 
                    realname: '',
                    // 
                    face: require('@/assets/images/default-face.jpg'),
                    // 
                    gender: '',
                    // 
                    birthdate: '',
                    // 
                    mobile: '',
                    // 
                    email: '',
                    // 
                    companyName: '',
                    // 
                    job: '',
                    // 
                    profession: '',
                    // 
                    province: '',
                    // 
                    city: '',
                    // 
                    area: '',
                    // 
                    workTime: '',
                    // 
                    isGraduate: false,
                },
                // 
                validate: {
                    realname: [{ required: true, message: '', trigger: 'blur'}],
                    birthdate: [{
                        validator: (rule, value, callback) => {
                            Validate.ValidBirthDate(value, callback, false);
                        },
                        trigger: 'change'
                    }],
                    mobile: [
                        { required: true, message: '', trigger: 'blur'},
                        { pattern: Common.regMobile, message: '', trigger: 'blur' }
                    ],
                    email: [
                        { required: true, message: '', trigger: 'blur'},
                        { pattern: Common.regEmail, message: '', trigger: 'change' }
                    ],
                    profession: [{
                        required: true,
                        validator: (rule, value, callback) => {
                            if(this.professionValue.length <= 0) return callback(new Error(''));
                            else return callback();
                        },
                        trigger: 'change'
                    }],
                    workTime: [{
                        required: true,
                        validator: (rule, value, callback) => {
                            // 
                            if(this.infoForm.isGraduate){
                                // 
                                this.infoForm.workTime = '';
                                // 
                                this.disabledWT = true;
                                // 
                                this.workTimePH = '';
                                return callback();
                            }
                            else{
                                this.disabledWT = false;
                                this.workTimePH = '';
                                if(value == '') return callback(new Error(''));
                                else{
                                    // 
                                    let nowDate = new Date();
                                    const compare = Common.CompareDate(value, nowDate);
                                    if (!compare) return callback(new Error(''));
                                    else return callback();
                                }
                            }
                        },
                        trigger: 'change'
                    }]
                },
            }
        },
        created() {
            // 
            Common.InitPicStore(this);
            Common.InitInputStore(this);
            // 
            this.jobList = JsonData.job;    
            // 
            this.professionList = JsonData.profession;            
            // 
            if(this.$route.query.id){
                // 
                this.userId = this.$route.query.id;
                this.$store.commit('SET_BREADCRUMB', [
                    { name: '', path: '/Home' },
                    { name: '', path: '/Examples/AccountList' },
                    { name: '', path: '' }
                ]);
                // 
                this.getDetail();
                this.pageType = 'edit';
            }
            else{
                this.pageLoading = false;
                this.$store.commit('SET_BREADCRUMB', [
                    { name: '', path: '/Home' },
                    { name: '', path: '/Examples/AccountList' },
                    { name: '', path: '' }
                ]);
            }
        },
        methods: {
            // 
            submit(form, type) {
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        // 
                        this.pageLoading = true;

                        this.infoForm.face = this.getImageUrl;
                        this.infoForm.companyName = this.companyName;

                        // 
                        this.infoForm.birthdate = this.dateFormat(this.infoForm.birthdate);
                        this.infoForm.workTime = this.dateFormat(this.infoForm.workTime);
                        
                        if(this.pageType == 'add'){
                            // 
                            Api.AddAccount(this.infoForm)
                            .then(res => {
                                // 
                                this.pageLoading = false;
                                if(res.code == 200){
                                    this.$Message.success({
                                        content: '!',
                                        onClose: () => {
                                            // 
                                            this.$router.push({ name: 'AccountList' });
                                        }
                                    }); 
                                }
                                else console.log(res);
                            })
                            .catch(err => this.$Message.warning(err.message))
                        }
                        else{
                            // 
                            Api.EditAccount(this.infoForm, this.userId)
                            .then(res => {
                                // 
                                this.pageLoading = false;
                                if(res.code == 200) this.$Message.success('!');
                                else this.$Message.warning(res.msg);
                            })
                            .catch(err => console.log(err))
                        }
                    }
                    else this.$Message.error('');
                })    
            },
            // 
            getDetail(){
                Api.GetAccInfo(this.userId)
                .then(res => {                    
                    // 
                    this.pageLoading = false;
                    const result = res.data.attributes;                    
                    if(res.code == 200){
                        // 
                        this.infoForm = result;
                        this.createdAt = res.data.createdAt;
                        // 
                        this.provinceValue = [this.infoForm.province, this.infoForm.city, this.infoForm.area];
                        // 
						this.$store.commit('SET_INPUT_VALUE', result.companyName);                  
                        // 
                        this.$store.commit('SET_IMAGE_URL', this.infoForm.face);
                        // 
                        if(this.infoForm.isGraduate){
                            this.disabledWT = true;
                            this.workTimePH = '';
                        }
                        // 
                        let str = this.infoForm.profession;
                        if(str != '')this.professionValue = this.infoForm.profession.split(",");
                        else this.professionValue = [];
                    }
                    else this.$Message.warning(res.msg);
                })
                .catch(err => console.log(err))
            },           
            // 
            getBirthDate(date) {
                console.log('get birth date:' + date);
                this.infoForm.birthdate = date;
            },
            // 
            checkProfess(checkedData){                
                let str = '';              
                for(let i = 0 ; i < checkedData.length ; i++){
                    str += checkedData[i] + ','
                }
                this.infoForm.profession = str.substring(0, str.length - 1);
            },
            // 
            dateFormat(value){
                if(value != '')
                    value = new Date(value).toLocaleDateString();
                return value;
            }
        }
    }
</script>

<style lang="less" scoped>    
    .m-operation {
        button, a {
            min-width: 80px;
            margin-left: 10px;
        }
    }
    .ivu-select.select-province {
        float: left;
        width: 32.5%;
        margin-right: 1%;
        &:nth-child(3n) {
            margin-right: 0;
        }
    }
</style>


