<template>
  	<div id="login" class="m-login">
       <Form ref="loginForm" :model="loginForm" :rules="validate" class="login-form">
       		<h3 class="form-title"><img :src="logo"/></h3>
	        <Form-item prop="username">
	            <Input v-model="loginForm.username" placeholder="">
	            	<Icon type="ios-person-outline" slot="prepend"></Icon>
	            </Input>
	        </Form-item>
	        <Form-item prop="password">
	            <Input type="password" v-model="loginForm.password" placeholder="">
	            	<Icon type="ios-locked-outline" slot="prepend"></Icon>
	            </Input>
	        </Form-item>
	        <Form-item>
	            <Checkbox v-model="remeberPwd"></Checkbox>
	            <!-- <router-link to="/SendVerifyCode" class="fr"></router-link> -->
	        </Form-item>
	        <Form-item>
	            <Button type="primary" long @click="submit('loginForm')" :loading="loading"></Button>
	        </Form-item>
	        <!-- <Form-item>
	            <Button type="primary" long @click=""></Button>
	        </Form-item> -->
	        <div style="text-align:center">:18888888888 :666666</div>
	    </Form>
	</div>
</template>

<script>
	import { SetCookie, SetLocalS, GetLocalS, Encrypt, Decrypt } from 'common/important.js'
	// Api
	import Api from 'api/api.js'

	export default {
		data(){
			return {
				// 
				loading: false,
				// logo
				logo: require('assets/images/logo-green.png'),
				// 
				loginForm:{
					// 
					username: '',
					// 
					password: '',
				},
				// 
				remeberPwd: false,
				// 
				validate: {
                    username: [{ required: true, message: '', trigger: 'blur' }],
                    password: [{ required: true, message: '', trigger: 'blur' }],
                }
			}
		},
		created() {
			// 
			if (GetLocalS('username')) {
				// 
				this.loginForm.username = GetLocalS('username');
				this.loginForm.password = Decrypt(GetLocalS('password'));
				this.remeberPwd = true;
			}
		},
		mounted(){
			const loginBg = document.getElementById('login');
			// 
			loginBg.style.height = document.body.scrollHeight + 'px';
			// 
            window.onresize = () => {
				loginBg.style.height = document.body.scrollHeight + 'px';
            }
		},
		methods:{
			// 
			submit (form){
                this.$refs[form].validate((valid) => {
                    if (valid) {
						Api.Login(this.loginForm)
						.then(res => {
							if(res.code == 200){
								const result = res.data;
								// tokencookie
								SetCookie('xl_admin_t', result.attributes.token);
								this.$store.commit('SET_USER_TOKEN', result.attributes.token);
								// 
								if (this.remeberPwd) {
									// 
									SetLocalS('username', this.loginForm.username);
									SetLocalS('password', Encrypt(this.loginForm.password));
								}
								this.$Message.success('!');
								// 
								this.$router.push({ name: 'Main' });
							}
							else this.$Message.error('!');
						})
						.catch(err => this.$Message.error('!'));
                    }
                    else this.$Message.error('!');
                })
            },
		}
	}
</script>
<style lang="less" scoped>
	.m-login {
	    position: relative;
	    width: 100%;
	    height: 100%;
	    height: 100vh;
		min-height: 900px;
	    background-color: #4d5256;
	}

	.login-form{
	    position: absolute;
	    left: 50%;
		top: 50%;
	    width: 400px;
	    padding: 35px 35px 15px;
	    margin: auto;
		margin-top: -250px;
		margin-left: -200px;
	    background: #fff;
	    border: 1px solid #eaeaea;
	    border-radius: 5px;
		-moz-border-radius: 5px;
		-webkit-border-radius: 5px;
		box-shadow: 0 0 25px #555;
	}

	.form-title{
		font-size: 24px;
		font-weight: 400;
		color: #555;
		margin: 0 auto 20px;
		text-align: center;
		font-weight: 700;

		img{
			display: inline-block;
		}
	}
</style>



