import Common from 'common/common.js'

export default {
	data() {
		return {
			// 
            isQuery: false,
			// --iViewDatePickerdaterangeVueVue2.4
			// dateSetting: {
			// 	shortcuts: [
			// 		{
			// 			text: '',
			// 			value () { return Common.SetDate(1); }
			// 		},
			// 		{
			// 			text: '',
			// 			value () { return Common.SetDate(2); }
			// 		},
			// 		{
			// 			text: '',
			// 			value () { return Common.SetDate(3); }
			// 		}
			// 	]
			// },
		}
	},
	methods: {
		// --iViewDatePickerdaterangeVueVue2.4
		// getDate(date) {
		// 	console.log('get date:' + date);
		// 	this.queryForm.sTime = date[0] + ' 00:00:00';
		// 	this.queryForm.eTime = date[1] + ' 23:59:59';
		// },
		// 
		getStartDate(date) {
			console.log('get start date:' + date);
			this.queryForm.sTime = date;
		},
		// 
		getEndDate(date) {
			console.log('get end date:' + date);
			this.queryForm.eTime = date;
		},
		// 
		// form: getValid:
		query(form, getValid) {
			// 
			this.page.pageNo = 1;

			if(getValid == 'valid'){
				// 
				this.$refs[form].validate((valid)=>{
					// true            
					if (valid) this.getTableList(true);
					else this.$Message.warning('');
				})
			}
			else this.getTableList(true);
		},
		// 
		clearQuery(){		
			for(let key in this.queryForm){
				delete this.queryForm[key];
			}
		},
		// 
		resetQuery(form){
			this.clearQuery();
			this.$refs[form].resetFields();
			this.getTableList();
		},
		// 
		getFilterList(){
			if(this.queryForm.eTime == '') this.queryForm.eTime = (new Date()).toLocaleDateString();
			this.apiGetFilter()
			.then(res => {
			    this.pageLoading = false;
			    if(res.code == 200){
			        // 
					this.setListData(res.data);
					// 
					this.setPage(res.page);
			    }
			    else this.$Message.error('!');
			})
			.catch(err => console.log(err))
		},
		// 
		getAllList(){
			this.apiGetAll().then(res => {
				this.pageLoading = false;
				if(res.code == 200){
					// 
					this.setListData(res.data);
					// 
					this.setPage(res.page);
				}
				else this.$Message.error('!');
			})
			.catch(err => console.log(err))
		},
	}
}


