<template>
    <div class="g-content">
        <!--  -->
        <h2 class="m-title"></h2>
        <div class="m-content">
            <Row class="m-sidebar-cont">
                <Col span="4">
                    <Sidebar :active-name="activeName" :menu-list="MenuList"></Sidebar>
                </Col>
                <Col span="20">
                    <router-view></router-view>
                </Col>
            </Row>
            <div class="m-desc">
                <p class="m-hint">*iViewMenu 2</p>
                <p>&nbsp;</p>
                <p></p>
                <p>1. activeNamenameString"Home"</p>
                <p>2. MenuListArray"Home"</p>
                <pre>
                [
                    {
                        <font color="#00c5a3">// menu-itemname</font>
                        name: 'AccountManage',
                        <font color="#00c5a3">// iViewIcontype</font>
                        icon: 'navicon-round',
                        <font color="#00c5a3">// menu-title</font>
                        text: '',
                        <font color="#00c5a3">// submenu</font>
                        submenu: [
                            {
                                <font color="#00c5a3">// submenu-itemname</font>
                                name: 'AccountList',
                                <font color="#00c5a3">// submenu-title</font>
                                text: '',
                            },
                        ]
                    },
                ]
                </pre>
            </div> 
        </div>
    </div>
</template>

<script>
    // 
	import Sidebar from 'components/Sidebar/Sidebar'

    export default {
        components:{ Sidebar },
        data () {
            return {
                // name
                activeName: 'PageOne',
                // 
                MenuList: [
                    {
                        name: 'PageOne',
                        icon: 'navicon-round',
                        text: '-1',
                        submenu: []
                    },
                    {
                        name: 'TestTwo',
                        icon: 'navicon-round',
                        text: '-2',
                        submenu: [
                            {
                                name: 'PageTwo',
                                text: '-2.1'
                            },
                            {
                                name: 'PageThree',
                                text: '-2.2'
                            },
                        ]
                    },
                    {
                        name: 'TestThree',
                        icon: 'navicon-round',
                        text: '-3',
                        submenu: [
                            {
                                name: 'PageFour',
                                text: '-3.1'
                            }
                        ]
                    },
                ],
            }
            },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/Components/Sidebar' }
            ]);
        }
    }
</script>

<style lang="less" scoped>
    @import "../../../assets/less/page_desc";
    .m-sidebar-cont{
        border:1px solid #a5a7a9;
        border-radius: 4px;
    }
    .m-desc{
        width:100%;
    }

    .m-content{
        padding: 0 20px 20px 20px;
    }
</style>



