<template>
    <div class="g-content">
        <!--  -->
        <div class="m-query-form fr">
            <Form ref="queryForm" :model="queryForm">
                <Form-item class="query-item">
                    <Input v-model="queryForm.id" placeholder=""></Input>
                </Form-item>
                <Form-item class="query-item">
                    <Input v-model="queryForm.title" placeholder=""></Input>
                </Form-item>
                <Form-item class="query-item">
                    <Select v-model="queryForm.type" placeholder="">
                        <Option value=""></Option>
                        <Option value="1"></Option>
                        <Option value="2"></Option>
                    </Select>
                </Form-item>
                <Form-item class="fl">
                    <Button class="query-button" type="primary" @click="query('queryForm', '')"></Button>
                </Form-item>
            </Form>
        </div>
        <!--  -->
        <div class="m-operation fl">
            <Button class="operation-btn" type="primary" @click="addSection"></Button>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="warning" @click="deleteData"></Button>
        </div>
        <div class="clearfix"></div>
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>
            <!--  -->
            <Table
                class="m-table-list"
                border
                :columns="userList"
                :data="listData"
                @on-selection-change="setSelectList"
            ></Table>
        </div>
        <!--  -->
        <Page
            class-name="m-page"
            show-elevator
            show-sizer
            show-total
            :total="page.dataCount"
            :page-size="page.pageSize"
            :current="page.pageNo"
            :page-size-opts="page.pageSizeOpts"
            @on-change="changePage"
            @on-page-size-change="changePageSize"
        >
        </Page>
        <div class="clearfix"></div>
        <!-- -->
        <Modal v-model="showModal" width="500" @on-cancel="closeModal('paramsForm')">
            <p slot="header">
                <span v-text="paramsForm.title == '' ? '' : ''"></span>
            </p>
            <div>
                <Form ref="paramsForm" :model="paramsForm" :rules="validate" :label-width="100">
                    <Form-item label="" prop="title">
                        <Input v-model="paramsForm.title"></Input>
                    </Form-item>
                    <Form-item label="" prop="type">
                        <Radio-group v-model="paramsForm.type">
                            <Radio label="1"></Radio>
                            <Radio label="2"></Radio>
                        </Radio-group>
                    </Form-item>
                </Form>
            </div>
            <div slot="footer">
                <Button size="large" @click="closeModal('paramsForm')"></Button>
                <Button type="primary" size="large" @click="operation('paramsForm', operateType)"></Button>
            </div>
        </Modal>
    </div>
</template>

<script>
    // 
    import Loading from '@/components/Common/Loading'
    // Api
    import Api from '@/api/api.js'
    // 
    import tableSetting from 'common/table_setting.js'
    // 
    import TableQuery from 'mixins/table_query.js'
    // 
    import TableOperate from 'mixins/table_operate.js'
    // 
    import Page from 'mixins/page.js'

    export default {
        components: { Loading },
        mixins: [ TableQuery, TableOperate, Page ],
        computed: {
            // 
            apiGetAll(){
                return () => Api.GetSecList(this.page.pageNo, this.page.pageSize);
            },
            // 
            apiGetFilter(){
                return () => Api.FilterSecList(this.queryForm, this.page.pageNo, this.page.pageSize);
            },
            // 
            apiAdd(){
                return () => Api.AddSection(this.paramsForm);
            },
            // 
            apiEdit(){
                return () => Api.EditSection(this.paramsForm, this.editId);
            },
            // 
            apiDelete(){
                return () => Api.DeleteSec(this.selectList);
            },
        },
        data() {
            return {
                // 
                pageLoading: true,
                // 
                showModal: false,
                // 
                paramsForm: {
                    title: '',
                    type: ''
                },
                // id
                editId: '',
                // :1, 2
                operateType: 0,
                // 
                validate: {
                    title:[{ required: true, message: '', trigger: 'blur' }],
                },
                // 
                queryForm: {
                    // 
                    id: '',
                    // 
                    title: '',
                    // 
                    type: ''
                },
                // 
                userList:[
                    {
                        title: '',
                        type: 'selection',
                        key: 'selectAll',
                        width: '60',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'id',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'title',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'type',
                        align: 'center',
                        render: (h, params) => {
                            return h('span', {}, params.row.type == 1 ? '' : '');
                        }
                    },
                    {
                        title: '',
                        key: 'operation',
                        width: '200',
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [                                
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        minWidth: '64px',
                                        marginRight: '10px'
                                    },
                                    on: {
                                        click: () => {
                                            // 
                                            this.openModel(params.row);
                                            this.operateType = 2;
                                        }
                                    }
                                }, ''),
                                tableSetting.gotoDetail(h, params, this, '', 'ContManage'),
                            ]);                          
                        }
                    }
                ],
                //
                listData:[],
            }
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '' }
            ]);
            // 
            this.getTableList();
        },
        methods: {
            // 
            getTableList(query){
                this.pageLoading = true;
                // 
                if(query){
                    this.isQuery = true;
                    this.getFilterList();
                }
                else{
                    this.isQuery = false;
                    this.getAllList();
                }
            },
            // 
            setListData(result){
                if(result.length > 0){
                    this.listData = result.map(item => {
                        return {
                            id: item.id,
                            title: item.attributes.title,
                            type: item.attributes.type,
                        }
                    });
                }
                else this.listData = [];
            },
            // 
            closeModal(name){
                this.showModal = false;
                // 
                this.$refs[name].resetFields();
            },
            // 
            openModel(params){
                this.showModal = true;
                this.paramsForm.title = params.title;
                this.paramsForm.type = params.type;
                this.editId = params.id;
            },
            // 
            operation(name, type){
                // 
                this.$refs[name].validate((valid)=>{
                    if(valid){
                        this.paramsForm.type = parseInt(this.paramsForm.type);
                        // 
                        if(type == 1) this.addData();
                        else if(type == 2) this.EditData();
                        // 
                        setTimeout(() => {
                            this.closeModal(name);
                        }, 500);
                    }
                    else this.$Message.error('');
                })
            },
            addSection(){
                this.showModal = true;
                this.operateType = 1;
            },
        }
    }
</script>

<style lang="less" scoped>
    @import "../../../assets/less/table_list";
</style>


