<template>
    <div class="m-case-list">
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>
            <div class="case-pic-list">
                <!--  -->
                <swiper :options="swiperOption" ref="caseList">
                    <!--  -->
                    <swiper-slide v-for="(item, index) in caseList" :key="index" >
                        <div :class="caseIndex === index ? 'swiper-active': 'swiper-div'" @click="showThisCase(index)">
                            <img :src="item.img" @error="notFoundPic"/>
                            <span>{{ item.title }}</span>
                        </div>
                    </swiper-slide>
                    <!--  -->
                    <swiper-slide>
                        <div class="u-add-btn" @click="addCase">+ </div>
                    </swiper-slide>
                </swiper>
                <!--  -->
                <a class="swiper-btn-prev" @click="swiperPrev()"></a>
                <a class="swiper-btn-next" @click="swiperNext()"></a>
            </div>
            <div v-if="caseList.length > 0" class="case-cont">
                <div class="case-cont-item">
                    <span class="item-label"></span><span class="item-info">{{ caseInfo.title }}</span>
                </div>
                <div class="case-cont-item">
                    <span class="item-label"></span><img class="item-info" :src="caseInfo.img" @error="notFoundPic"/>
                </div>
                <div class="case-cont-item">
                    <span class="item-label"></span><span class="item-info">{{ caseInfo.brand }}</span>
                </div>
                <div class="case-cont-item">
                    <span class="item-label"></span><span class="item-info">{{ caseInfo.updateTime }}</span>
                </div>
                <!--  -->
                <div class="m-operation">                    
                    <Button class="fr" type="primary" @click="editCase"></Button>
                    <Button class="fr" type="primary" @click="deleteCase"></Button>
                    <div class="clearfix"></div>
                </div>
            </div>
        </div>
        <!-- -->
        <Modal v-model="showModal" width="500" @on-cancel="closeModal('paramsForm')">
            <p slot="header">
                <span v-text="paramsForm.title == '' ? '' : ''"></span>
            </p>
            <div>
                <Form ref="paramsForm" :model="paramsForm" :rules="validate" :label-width="100">
                    <Form-item label="" prop="title">
                        <Input v-model="paramsForm.title" placeholder=""></Input>
                    </Form-item>
                    <Form-item label="" prop="img">
                        <!-- -- -->
                        <SingleImage :preview="true"></SingleImage>
                    </Form-item>
                    <Form-item label="" prop="brand">
                        <Input v-model="paramsForm.brand" placeholder=""></Input>
                    </Form-item>
                </Form>
            </div>
            <div slot="footer">
                <Button size="large" @click="closeModal('paramsForm')"></Button>
                <Button type="primary" size="large" @click="operation('paramsForm', operateType)"></Button>
            </div>
        </Modal>
    </div>
</template>

<script>
    // 
    import Loading from 'components/Common/Loading'
    import SingleImage from 'components/Image/UploadImage/SingleImage'
    // JS
    import Common from 'common/common.js'
    // Api
    import Api from 'api/api.js'
    // Vuex
    import { mapGetters } from 'vuex'
    // 
    import 'swiper/dist/css/swiper.css'
    
    export default {
        components: { Loading, SingleImage },
        // productId
        props: {
            // 
            productId:{
                type: String,
                default: '',
            },
        },
        computed:{
            ...mapGetters(['getImageUrl']),
			// swiper
			swiper(){
				return this.$refs.caseList.swiper;
			}
        },
        data() {
            return {
                // 
                pageLoading: false,
                // 
                swiperOption: {
					notNextTick: true,
					prevButton:'.swiper-button-prev',
					nextButton:'.swiper-button-next',
					slidesPerView: 5,
					spaceBetween: 30,
					freeMode: true
                },
                // 
				caseIndex: 0,
				// 
                caseList: [],
                // :1, 2
                operateType: 0,
                // 
                showModal: false,
                // 
                caseInfo:{
					img: '',
					title: '',
					brand: '',
                    updateTime: '',
                },
                // 
				paramsForm:{
					img: '',
					title: '',
					brand: '',
                    updateTime: '',
                },
                // 
                validate: {
                    title: [{ required: true, message: '', trigger: 'blur'}],
                },
            }
        },
        created() {
            // 
            Common.InitPicStore(this);
            // 
			this.getCaseList(this.productId);
        },
        mounted() {
			// swiper
			console.log('this is current swiper instance object', this.swiper);
	    },
        methods: {
            // 
			swiperPrev(){
                this.swiper.slidePrev();
			},
			// 
			swiperNext(){
				this.swiper.slideNext();
            },
            // ID
            getCaseList(id){
                Api.GetCaseList(this.productId)
                .then(res => {
                    if(res.code == 200){
                        const result = res.data;
                        // 
                        this.caseList = res.data;
                        // 
	    				this.showThisCase(this.caseIndex);
                    }
                    else console.log(res);
                })
                .catch(err => console.log(err));
            },
            // 
            showThisCase(index){
                this.caseIndex = index;
                this.caseInfo.title = this.caseList[index].title;
                this.caseInfo.brand = this.caseList[index].brand;
                this.caseInfo.updateTime = this.caseList[index].updateTime;
                this.caseInfo.img = this.caseList[this.caseIndex].img;
            },
            // 
            closeModal(name){
                this.showModal = false;
                // 
                this.$refs[name].resetFields();
            },
            // 
            openModel(){
                this.showModal = true;
            },
            // 
            editCase(){                
                this.operateType = 2;
                this.paramsForm.title = this.caseList[this.caseIndex].title;
                this.paramsForm.brand = this.caseList[this.caseIndex].brand;
                this.$store.commit('SET_IMAGE_URL', this.caseList[this.caseIndex].img);
                this.openModel();
            },
            // 
            addCase(){                
                this.operateType = 1;
                this.paramsForm.title = '';
                this.paramsForm.brand = '';
                this.$store.commit('SET_IMAGE_URL', '');
                this.openModel();
            },
            // 
            deleteCase(){
                // 
                this.$Modal.confirm({
                    // 
                    okText: '',
                    // 
                    render: (h) => {
                        return h('p', {}, '');
                    },
                    // 
                    onOk: () => {
                        Api.DelCase(this.productId, this.caseIndex)
                        .then(res => {
                            if(res.code == 200){
                                this.$Message.success('!');
                                // 
                                this.getCaseList(this.productId);
                            }
                            else console.log(res);
                        })
                        .catch(err => console.log(err));
                    }
                });
            },
            // 
            operation(name, type){
                // 
                this.$refs[name].validate((valid)=>{
                    if(valid){
                        // 
                        this.paramsForm.img = this.getImageUrl;
                        // ,
                        this.paramsForm.updateTime = Common.FormatDate(new Date());
                        // 
                        if(type == 1){
                            Api.AddCase(this.paramsForm, this.productId)
                            .then(res => {
                                if(res.code == 200){
                                    this.closeModal(name);
                                    this.$Message.success('!');
                                    // 
                                    this.getCaseList(this.productId);
                                }
                                else console.log(res);
                            })
                            .catch(err => console.log(err));
                        }
                        else if(type == 2){
                            Api.EditCase(this.paramsForm, this.productId, this.caseIndex)
                            .then(res => {
                                if(res.code == 200){
                                    this.closeModal(name);
                                    this.$Message.success('!');
                                    // 
                                    this.getCaseList(this.productId);
                                }
                                else console.log(res);
                            })
                            .catch(err => console.log(err));
                        }
                    }
                    else this.$Message.error('');
                })
            },
            // 
            notFoundPic(event){
                Common.SetDefaultPic(event, 2);
            },
        }
    }
</script>

<style lang="less">
    @import "../../../../assets/less/color";
    .m-operation {
        button, a {
            min-width: 80px;
            margin-left: 10px;
        }
    }

    .case-pic-list{
		background: #f5f5f5;
		padding: 20px 40px;
        position: relative;
        border-radius: 4px;
    }

    .case-cont{
        margin-top: 30px;
    }
    
    .case-cont-item{
        margin-bottom:20px;
        &::after{
            clear: both;
            content: "";
            display:block;
        }
        .item-label{
            float: left;
            width: 100px;
            text-align: right;
            display: inline-block;
            margin-right: 20px;
        }
        .item-info{
            float: left;
        }

        img{
            border: 1px solid #eee;
            border-radius: 4px; 
        }
    }
    
    .swiper-container {
        width: 100%;
        height:230px;        
        background: #f5f5f5;
    }

    .swiper-div{
    	height:100%;
    	border:2px solid @table_border_color;
    }

    .swiper-active{
    	height:100%;
    	border:2px solid @base_color;
    }
    .swiper-slide {
        text-align: center;
        font-size: 18px;
        background: #fff;

        cursor:pointer;

        img{
	    	width:100%;
	    	height:200px;
	    	display: block;
	    }

	    span{
	    	display:block;
	    	text-align:center;
	    	font-size:12px;
	    	line-height:28px;
	    	overflow:hidden;
	    	white-space:nowrap;
	    	overflow:hidden;
	    	text-overflow:ellipsis;
        }
        
        .u-add-btn{
            height:100%;
            line-height:228px;
            font-size:14px;
            border:1px dashed #999;
        }
    }

	.swiper-btn-prev, .swiper-btn-next {
	    background-image: url('../../../../assets/images/icon-arrows.png');
	    position: absolute;
	    top: 50%;
	    width: 23px;
	    height: 37px;
	    margin-top: -22px;
	    z-index: 10;
	    cursor: pointer;
	}

	.swiper-btn-prev{
		left: 8px;
	}

	.swiper-btn-next{
		right: 8px;
		background-position: -23px 0;
	}
</style>


