<template>
    <div id="singleImage" class="m-Upload">
        <!--  -->
        <div v-if="getImageUrl != ''" class="upload-show">
            <div class="upload-img-wrap"><img class="upload-image" :src="getImageUrl" alt="" @error="notFoundPic"/></div>
            <!--  -->
            <div v-if="preview" class="upload-mask">
                <Icon type="eye" @click.native="viewImage"></Icon>
            </div>
        </div>
        <!--  -->
        <div>
            <Button type="ghost" :loading="loading" icon="ios-cloud-upload-outline" @click="uploadClick"></Button>
            <span class="size-hint" v-if="sizeHint"> {{ sizeHint }}</span>
            <input ref="imgFile" type="file" :accept="format" hidden @change="selectFile"/>
        </div>
        <!--   -->
        <template v-if="uploadStatus != 'finished'">
            <Progress v-if="showProgress" :percent="percentage" hide-info :stroke-width="3" style="width:340px;"></Progress>
        </template>
        <br/>
        <!--  -->
        <span class="hint">
            * jpgjpegpng
            <span v-if="fileSize < 1024">{{ fileSize }}kb</span>
            <span v-else>{{ Math.floor(fileSize/1024) }}M</span>
            
        </span>
        <div class="clearfix"></div>
        <!--  -->
        <Modal title="" class="m-view-img" v-model="showModal" width="600">
            <img :src="getImageUrl" @error="notFoundPic"/>
            <div slot="footer"></div>
        </Modal>
    </div>
</template>

<script>
    // JS
    import Common from 'common/common.js'
    // 
    import UploadImg from 'mixins/upload_img.js'
    // axios
    import axios from 'axios'
    // Vuex
    import { mapGetters } from 'vuex'

    export default {
        name: 'singleImage',
        mixins: [ UploadImg ],
        // 
        props: {
            // 
            preview:{
                type: Boolean,
                default: false
            },
            // 
            sizeHint:{
                type: String,
                default: '100*100px'
            },
            // KB
            fileSize:{
              type: Number,
              default: 150
            }
        },
        computed: {
            ...mapGetters([
                // 
                'getImageUrl',
            ])
        },
        data () {
            return {
                // 
                loading: false,
                // 
                showModal: false,
                // 
                format: ['image/jpg', 'image/jpeg', 'image/png'],
                // finished
                uploadStatus: '',
                // 
                showProgress: false,
            }
        },
        methods: {}
    }
</script>

<style lang="less" scoped>
    @import "../../../assets/less/upload_img";
</style>


