<template>
    <div class="g-content">
        <!--  -->
        <div class="m-operation">
            <Button class="operation-btn" type="primary" @click="openModel"></Button>
            <Button class="operation-btn" type="warning" :disabled="selectList.length == 0" @click="deleteData"></Button>
            <Button class="operation-btn" type="ghost" @click="$router.go(-1)"></Button>
        </div>
        <!--  -->
        <table ref="contTable" class="m-table" width="100%" cellpadding="0" cellspacing="0">
            <!--  -->
            <th v-for="(item, index) in title" :key="index" :align="item.align" :style="{ 'width' : item.width + 'px'}">
                <Checkbox v-if="item.type == 'CheckBox'" v-model="checkAll" @on-change="selectAll(checkAll)"></Checkbox>
                <span v-else>{{ item.title }}</span>
            </th>
            <!--  -->
            <tr v-for="(item, index) in listData" :key="index">
                <td v-for="(th, i) in title" :key="i" :align="th.align">
                    <!--  -->
                    <Checkbox v-if="th.type == 'CheckBox'" v-model="item.isCheck" @on-change="selectRow(index, item.isCheck)"></Checkbox>
                    <!--  -->
                    <span v-if="th.type == 'Text'">{{ item[th.key] }}</span>
                    <!--  -->
                    <Input v-if="th.type == 'Input'" v-model="item[th.key]"></Input>
                    <!--  -->
                    <div v-if="th.type == 'UploadImg'">
                        <div class="upload-img">
                            <img class="show-img" :src="item[th.key]" alt="" @error="notFoundPic"/>
                        </div>
                        <div class="upload-btn">
                            <Button type="ghost" :disabled="item[th.key] == ''" icon="ios-eye-outline" @click="viewImage(index)"></Button>
                            <Button type="ghost" icon="ios-cloud-upload-outline" @click="uploadClick(index)" style="margin-left:10px"></Button>                            
                        </div>
                    </div>
                    <!--  -->
                    <div v-if="th.type == 'Button'">
                        <Button
                            :key="index"
                            :type="th.button.type"
                            :size="th.button.size"
                            :style="{ 'min-width' : th.button.minWidth + 'px' }"
                            @click="saveThis(index)"
                        >
                        {{ th.button.text }}
                        </Button>
                    </div>
                </td>
            </tr>
        </table>
        <!--  -->
        <Page
            class-name="m-page"
            show-elevator
            show-sizer
            show-total
            :total="page.dataCount"
            :page-size="page.pageSize"
            :current="page.pageNo"
            :page-size-opts="page.pageSizeOpts"
            @on-change="changePage"
            @on-page-size-change="changePageSize"
        >
        </Page>
        <div class="clearfix"></div>
        <!--  -->
        <input ref="imgFile" type="file" :accept="format" hidden @change="selectFile"/>
        <!--  -->
        <Modal title="" v-model="showImgModal">
            <img :src="imgUrl" style="width: 100%" @error="notFoundPic"/>
            <div slot="footer"></div>
        </Modal>
        <!--  -->
        <Modal title="" v-model="showAddModal" @on-cancel="closeModal('paramsForm')">
            <div>
                <Form ref="paramsForm" :model="paramsForm" :rules="validate" :label-width="100">
                    <Form-item label="" prop="title">
                        <Input v-model="paramsForm.title"></Input>
                    </Form-item>
                    <Form-item label="">
                        <!-- -- -->
                        <SingleImage :preview="false"></SingleImage>
                    </Form-item>
                    <Form-item label="">
                        <Input v-model="paramsForm.url"></Input>
                    </Form-item>
                </Form>
            </div>
            <div slot="footer">
                <Button size="large" @click="closeModal('paramsForm')"></Button>
                <Button type="primary" size="large" @click="add('paramsForm')"></Button>
            </div>
        </Modal>
    </div>
</template>

<script>
    // 
    import SingleImage from 'components/Image/UploadImage/SingleImage'
    // JS
    import Common from 'common/common.js'
    // Api
    import Api from '@/api/api.js'
    // 
    import TableQuery from 'mixins/table_query.js'
    // 
    import TableOperate from 'mixins/table_operate.js'
    // 
    import Page from 'mixins/page.js'
    // Vuex
    import { mapGetters } from 'vuex'
    // axios
    import axios from 'axios'
      
    export default {
        components: { SingleImage },
        mixins: [ TableQuery, TableOperate, Page ],
        computed: {
            ...mapGetters([ 'getImageUrl' ]),
            // 
            apiGetAll(){
                return () => Api.GetContList(this.page.pageNo, this.page.pageSize);
            },
            // 
            apiAdd(){
                return () => Api.AddContent(this.paramsForm);
            },
            // 
            apiDelete(){
                return () => Api.DeleteCont(this.selectList);
            },
            // 
            apiEdit(){
                return () => Api.EditContent(this.paramsForm, this.editId);
            },
        },
        data(){
            return{
                //
                checkAll: false,
                // 
                showImgModal: false,
                // Url
                imgUrl: '',
                // 
                rowIndex: -1,
                // 
                maxSize: 2048,
                // 
                format: ['image/jpg', 'image/jpeg', 'image/png'],
                // 
                paramsForm: {
                    title: '',
                    img: '',
                    url: '',
                },
                // 
                validate: {
                    title:[{ required: true, message: '', trigger: 'blur' }],
                },
                // id
                editId: '',
                // 
                showAddModal: false,
                // 
                title: [
                    {
                        title: '',
                        key: 'selecAll',
                        align: 'center',
                        type: 'CheckBox',
                        width: '50'
                    },
                    {
                        title: '',
                        key: 'id',
                        align: 'center',
                        type: 'Text',
                        width: '100'
                    },
                    {
                        title: '',
                        key: 'title',
                        align: 'center',
                        type: 'Input'
                    },
                    {
                        title: '',
                        key: 'img',
                        align: 'center',
                        type: 'UploadImg',
                        width: '300'
                    },
                    {
                        title: '',
                        key: 'url',
                        align: 'center',
                        type: 'Input'
                    },
                    {
                        title: '',
                        key: 'operate',
                        align: 'center',
                        type: 'Button',
                        button: {
                            type: 'primary',
                            size: 'small',
                            minWidth: '64',
                            text: '',
                            click: 'save'
                        },
                        width: '100',
                    },
                ],
                // 
                listData: [
                    {
                        id: '1001',
                        title: '',
                        img: '',
                        url: 'http://fanyi.baidu.com/static/translation/img/header/logo_cbfea26.png'
                    },
                    {
                        id: '1002',
                        title: '',
                        img: '',
                        url: 'http://fanyi.baidu.com/static/translation/img/header/logo_cbfea26.png'
                    },
                ],
            }
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/Examples/SectionList' },
                { name: '', path: '' }
            ]);
            // 
            Common.InitPicStore(this);
            // 
            this.getTableList();
            // 
            this.initData(this.listData);
        },
        methods: {
            // 
            selectAll(check){
                this.listData.forEach(item => {                    
                    if(check){
                        item.isCheck = true;
                        // 
                        this.selectList.push(item.id);
                    }
                    else{
                        item.isCheck = false;
                        // 
                        this.clearSelect();
                    }
                })
            },
            // 
            selectRow(index, check){
                if(check){
                    this.selectList.push(this.listData[index].id);
                }
                else{
                    this.selectList.forEach((item, i) => {           
                        if(item == this.listData[index].id) this.selectList.splice(i,1);
                    })
                }
            },
            // 
            initData(data){
                // dataisCheckfalse
                if(data.length > 0){
                    data.forEach(item => {
                        item.isCheck = false;
                    })
                }
            },
            // 
            viewImage(index){
                this.showImgModal = true;
                this.imgUrl = this.listData[index].img;
            },
            // 
            uploadClick(index){
                // 
                this.$refs.imgFile.click();
                this.rowIndex = index;
            },
            // 
            selectFile(event){
                // 
                const fileList = this.$refs.imgFile.files;
                if(fileList.length > 0){
                    let fileName = fileList[0].name;
                    let fileSize = Math.floor(fileList[0].size / 1024);
                    // 
                    if(fileSize > this.maxSize){
                        this.$Notice.warning({
                            title: '',
                            desc: ' ' + fileName + '  ' + 2 + 'M'
                        });
                        return false;
                    }
                    this.uploadFile(fileList[0]);
                }
                else console.log('');
            },
            // 
            uploadFile(file){
                // formData
                let params = new FormData();
                // tokentokentoken
                params.append('token', Common.UPLOAD_TOKEN);
                params.append('file', file);
                
                // incorrect region, please use up-z2.qiniu.com
                axios.post('http://upload.qiniu.com/', params, { emulateJSON: true})
                .then(res => {
                    // 
                    this.listData[this.rowIndex].img = Common.UPLOAD_URL + res.data.hash;
                    this.$Notice.success({ title: '' });
                })
                .catch(err => {              
                    this.$Notice.error({ title: '' });
                })
            },
            // 
            getTableList(query){
                this.getAllList();
            },
            // 
            setListData(result){
                if(result.length > 0){
                    this.listData = result.map(item => {
                        return {
                            id: item.id,
                            title: item.attributes.title,
                            img: item.attributes.img,
                            url: item.attributes.url,
                        }
                    });
                }
                else this.listData = [];
            },
            // 
            saveThis(index){
                this.editId = this.listData[index].id;
                this.paramsForm.title = this.listData[index].title;
                this.paramsForm.img = this.listData[index].img;
                this.paramsForm.url = this.listData[index].url;
                // 
                this.EditData();
            },
            // 
            openModel(){
                this.showAddModal = true;
                this.paramsForm.title = '';
                this.paramsForm.img = '';
                this.paramsForm.url = '';                
            },
            // 
            closeModal(name){
                this.showAddModal = false;
                // 
                this.$refs[name].resetFields();
            },
            // 
            add(name){
                // 
                this.$refs[name].validate((valid)=>{
                    if(valid){
                        // 
                        this.paramsForm.img = this.getImageUrl;
                        // 
                        this.addData();
                        // 
                        setTimeout(() => {
                            this.closeModal(name);
                        }, 500);                  
                    }
                    else this.$Message.error('');
                })
            },
            // 
            notFoundPic(event){
                Common.SetDefaultPic(event, 2);
            },
        }
    }
</script>

<style lang="less" scoped>
    @import "../../../assets/less/table_list";
    @import "../../../assets/less/color";
    .m-table{
        position: relative;
        border: 1px solid @table_border_color;
        border-bottom: 0;
        border-right: 0;
        font-size: 12px;

        td,th{
            box-sizing: border-box;
            text-overflow: ellipsis;
            vertical-align: middle;
            border-bottom: 1px solid @table_border_color;
            border-right: 1px solid @table_border_color;
            padding: 12px;
        }

        th{
            background-color: #f8f8f9;
        }

        .show-img{
            width: auto;
            max-height: 120px;
            max-width: 90%;
            margin-bottom: 10px;
            border-radius: 4px;
            border: 1px solid rgb(228, 229, 231);
        }   
    }
</style>


